import { useState } from "react";
import { Layout } from "@/components/Layout";
import { DashboardContent } from "@/components/DashboardContent";
import { Home, BarChart3, Users, Settings, FileText, Package } from "lucide-react";

// Navigation configuration
const navigation = [
  { label: "Dashboard", href: "/dashboard", icon: <Home className="w-5 h-5" /> },
  { label: "Analytics", href: "/analytics", icon: <BarChart3 className="w-5 h-5" /> },
  { label: "Users", href: "/users", icon: <Users className="w-5 h-5" /> },
  { label: "Products", href: "/products", icon: <Package className="w-5 h-5" /> },
  { label: "Reports", href: "/reports", icon: <FileText className="w-5 h-5" /> },
  { label: "Settings", href: "/settings", icon: <Settings className="w-5 h-5" /> },
];

// Mock user data - replace with real auth data
const mockUser = {
  name: "John Doe",
  email: "john@mesobai.com",
  tenant_id: "TENANT-001",
};

export default function App() {
  const [currentPath] = useState("/dashboard");

  return (
    <Layout
      nav={navigation}
      pathname={currentPath}
      user={mockUser}
      isAffiliate={false}
      isAdmin={false}
    >
      <DashboardContent />
    </Layout>
  );
}

